@import url(https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  margin: 0;
  background-color: rgb(241,241,241);
  height: auto;
  width: auto;
}
.login {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 120vh;
  background: url("https://lh3.googleusercontent.com/-akZlQ_AKQnQ/YNt9oi7QxmI/AAAAAAAAABw/ufYGyMSsd8I5d2E6v1aa4ju4ebJEH9mJACLcBGAsYHQ/230%252B%2BTRON_%2BLegacy%2BHD%2BWallpapers%2B%257C%2BBackground%2BImages.jpg");

}

.login__container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 700px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}

.login__logo {
  display: flex;
  justify-content: center;
  margin-top: -20px;

}

.login__desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: -50px;
  font-family: "TLucida Handwriting";
}


.login__desc > p {
  color:  rgb(99, 39, 196);
  text-align: center;
  font-weight: 500;
  font-size: 20px;
}

.login__auth {
  display: flex;
  margin-top: -50px;
}

.login__authOptions {
  display: flex;
  flex: 0.5 1;
  flex-direction: column;
  padding: 20px;
  border-right: 1px solid lightgray;
}

.login__authOption {
  display: flex;
  align-items: center;
  padding: 7px;
  margin-bottom: 15px;
  border: 1px solid lightgray;
  border-radius: 5px;
  cursor: pointer;
}

.login__authOption:hover {
  background-color: rgb(226, 226, 226);
}

.login__authOption > img {
  height: 30px;
  object-fit: contain;
}

.login__authOption > p {
  margin-left: 11px;
}

.login__authDesc {
  display: flex;
  align-items: center;
  padding: 7px;
  margin-bottom: 15px;
}
.login__authDesc > p {
  font-size: small;
  color: gray;
}

.login__emailPass {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  flex: 0.5 1;
  margin-left: 20px;
}

.login__label > h4 {
  padding: 5px;
}

.login__inputFields {
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.login__inputField {
  padding: 5px;
}

.login__inputField > input {
  padding: 10px;
  width: 100%;
  outline: none;
  border: none;
  background-color: rgb(236, 236, 236);
}

.login__forgButt {
  padding-left: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login__forgButt > small {
  color: gray;
  cursor: pointer;
}

.login__forgButt > small:hover {
  color: blue;
  text-decoration: underline;
}

.login__forgButt > button {
  padding: 9px;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: blue;
  color: white;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
}

.login__emailPass > button {
  padding: 9px;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: blue;
  color: white;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
  margin-top: 10px;
}

.login__lang {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
  margin-top: 30px;
  padding: 10px;
}

.login__lang > p {
  color: gray;
  margin-right: 5px;
  cursor: pointer;
  font-size: 13px;
}

.login__lang > p:hover {
  color: gray;
  margin-right: 5px;
  text-decoration: underline;
}

.login__lang > .MuiSvgIcon-root {
  color: gray;
}

.login__footer {
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.login__footer > p {
  color: gray;
  font-size: small;
  cursor: pointer;
}

.login__footer > p:hover {
  text-decoration: underline;
}
.QnA_content {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}
.Feed {
    display: flex;
    flex-direction: column;
    flex: 0.6 1;
}
.post {
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: white;
    margin-top: 10px;
    border: 1px solid lightgray;
    border-radius: 5px;
    box-shadow: 0px 5px 8px -9px solid rgab(0, 0, 0, 0.5);
  }
  
  .post_info {
  
    align-items: center;
  }
  
  .post_info > h4 {
    margin-left: 10px;
    cursor: pointer;
    font-size: 13px;
  }
  .post_info > h4:hover {
    text-decoration: underline;
  }
  
  .post_info > small {
    margin-left: 10px;
  }
  
  .post_body {
    display: flex;
    flex-direction: column;
  }
  
  .post_body > .post_question {
    margin-top: 10px;
    font-weight: bold;
    margin-bottom: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    flex: 1 1;
  }
  
  .post_question > .post_button {
    margin-left: auto;
    cursor: pointer;
    padding: 7px;
    background-color: blue;
    outline: none;
    border: none;
    color: lightgray;
    font-weight: 300;
    font-size: 14px;
    border-radius: 33px;
  }
  
  .post_button:hover {
    color: blue;
    background: lightgray;
  }
  
  .post_question > p:hover {
    text-decoration: underline;
  }
  
  .post_body > .post_answer > p {
    margin-bottom: 10px;
  }
  
  /* .post_image {
    background: rgb(0, 140, 255);
  } */
  
  .post_body > img {
    width: 100%;
    object-fit: contain;
    background-color: rgb(238, 238, 238);
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  .post_footer {
    display: flex;
    align-items: center;
    margin-top: 5px;
  }
  
  .post_footer > .MuiSvgIcon-root {
    color: gray;
    margin-right: 40px;
    cursor: pointer;
  }
  
  .post_footerAction {
    background-color: lightgray;
    padding: 5px;
    align-items: center;
    display: flex;
    justify-content: space-around;
    border-radius: 33px;
  }
  .post_footerAction > .MuiSvgIcon-root {
    color: gray;
    margin-right: 40px;
    cursor: pointer;
  }
  
  .post_footerAction > .MuiSvgIcon-root:hover {
    color: rgb(0, 140, 255);
    margin-right: 40px;
  }
  
  .post_footerLeft {
    margin-left: auto;
  }
  
  .post_footerLeft > .MuiSvgIcon-root {
    color: gray;
    cursor: pointer;
    margin-left: 30px;
  }
    .modal_question {
      display: flex;
      align-items: center;
      flex-direction: column;
    }
    
    .modal_question > h1 {
      color: #8f1f1b;
      font-weight: 600;
      margin-bottom: 10px;
    }
    
    .modal_question > p {
      color: gray;
      font-size: small;
    }
    
    .modal_question > p > .name {
      color: black;
      font-weight: bold;
    }
    
    .modal_answer {
      display: flex;
      padding-top: 20px;
      flex: 1 1;
    }
    
    .modal_answer > textarea {
      width: 100%;
      height: 200px;
      padding: 5px;
      font-size: 15px;
      color: blue;
    }
    
    
  .modal__button {
    display: flex;
    align-items: center;
    margin-right: -20px;
    border-top: 1px solid lightgray;
  }
  
  .modal__button > .cancle {
    margin-left: auto;
    border: none;
    margin-top: 5px;
    outline: none;
    color: gray;
    font-weight: 500;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
  }
  .modal__button > .add {
    border: none;
    outline: none;
    margin-top: 5px;
    background-color: rgb(0, 64, 255);
    color: white;
    font-weight: 700;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
  }
.qHeader {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1px;
    background-color: rgb(233, 148, 148);
    width: 100%;
  
    position: -webkit-sticky;
  
    position: sticky;
    z-index: 1000;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.5);
  }
  
  .qHeader_logo > img {
    height: 80px;
    margin-right: 20px;
  }
  
  .qHeader_icons {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-left:160px ;
    margin-top: -70px;
  }
  
  .qHeader_icons > .active > .MuiSvgIcon-root {
    fill: #8f1f1b;
  }
  
  .qHeader_icon > .MuiSvgIcon-root {
    font-size: xx-large;
    background-color: white;
    padding: 20px 60px;
    color: gray;
  
  }
  
  .qHeader_icon > .MuiSvgIcon-root:hover {
    background-color: grey;
    border-radius: 10px;
    color:#00ff37;
  }
  
  .qHeader_input {
    display: flex;
    align-items: center;
    border: 1px solid lightblue;
    padding: 5px;
    border-radius: 5px;
    margin-left: 5px;
    
  }
  
  .qHeader_input > input {
    background-color: transparent;
    outline: none;
    border: none;
    color: rgb(49, 49, 49);
  }
  
  .qHeader_input > .MuiSvgIcon-root {
    color: gray;
  }
  
  .qHeader_avatar {
    cursor: pointer;
  }
  .qHeader_Rem {
    display: flex;
    align-items: center;
    margin-left: 25px;
  }
  
  .qHeader_Rem > .MuiSvgIcon-root {
    font-size: xx-large;
    color: rgb(255, 7, 7);
    margin-left: 25px;
    cursor: pointer;
  }
  .qHeader_Rem > .MuiSvgIcon-root:hover {
    color: #fd170f;
  }
  
  .qHeader_Rem > .MuiButton-root {
    color: white;
    background: rgb(11, 106, 184);
    text-transform: inherit;
    border-radius: 20px;
    margin-left: 25px;
  }
  
  .qHeader_Rem > .MuiButton-root:hover {
    color: white;
    background-color: rgb(81, 160, 250);
  }
  .modal__title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    border-bottom: 1px solid rgba(187, 187, 187, 0.5);
  }
  
  .modal__title > h5 {
    color: gray;
    font-size: 20px;
    cursor: pointer;
    font-weight: 500;
    margin-right: 30px;
  }
  
  .modal__title > h5:hover {
    color: #b92b27;
  }
  .modal__info {
  display: flex;
  align-items: center;
  margin-top: 30px;
  }
  
  .modal__info > p {
  margin-left: 10px;
  font-size: small;
  color: gray;
  }
  
  .modal__info > .modal__scope {
  display: flex;
  align-items: center;
  color: rgb(98, 98, 98);
  padding: 5px;
  margin-left: 10px;
  background-color: rgb(230, 230, 230);
  border-radius: 33px;
  cursor: pointer;
  }
  .modal__Field {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  flex: 1 1;
  }
  
  .modal__Field > .modal__fieldLink {
  color: gray;
  display: flex;
  margin-top: 10px;
  align-items: center;
  }
  
  .modal__Field > .modal__fieldLink > input {
  flex: 1 1;
  border: none;
  outline: none;
  margin-left: 5px;
  } 
  .modal__buttons {
  display: flex;
  align-items: center;
  margin-top: 260px;
  margin-right: -20px;
  border-top: 1px solid lightgray;
  }
  
  .modal__buttons > .cancle {
  margin-left: auto;
  border: none;
  margin-top: 5px;
  outline: none;
  color: gray;
  font-weight: 500;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
  }
  .modal__buttons > .add {
  border: none;
  outline: none;
  margin-top: 5px;
  background-color: rgb(0, 64, 255);
  color: white;
  font-weight: 700;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
  }
.QnAbox {
    display: flex;
    flex-direction: column;
    padding : 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 5px;
    cursor: pointer;
}
.QnAbox :hover {
border: 1px solid rgb(175,175,175);
}
.box_info {
    display: flex;
    flex-direction: center;
}
.box_info > h5 {
    color : rgb(129,129,129);
    font-weight: 300;
    margin-left: 10px;
}
.box_QnA {
    display:flex;
    margin-top:8px;
}
.box_QnA > p {
    color:gray;
    font-weight :bold;
    font-size:large;
}

.SidebarOptions {
    display :flex;
    flex-direction: column;
}
.SidebarOption {
    display:flex;
    align-items: center;
    padding: 8px;
    cursor: pointer;
}
.SidebarOption :hover {
    background-color: rgb(238, 84, 84);
    border-radius: 5px;
}
.SidebarOption > img {
    height: 25px;
    margin-left: 20px;
    border-radius: 5px;
}
.SidebarOption > p {
    color: rgb(121, 93, 77);
    font-weight: 400;
    margin-left: 15px;
}
.SidebarOption > .MuiSvgIcon-root {
    color: grey;
    font-size : medium;
    margin-left: 20px;
}
.Sidebar {
    margin-right: 10px;
    flex: 0.2 1;
}
.widget_contents {
    display: flex;
    flex-direction: column;
    cursor: pointer;
  }
  
  .widget_content {
    margin: 5px;
    display: flex;
    padding: 5px;
    border-bottom: 1px solid lightgray;
  }
  
  .widget_content > img {
    height: 30px;
    border-radius: 5px;
  }
  
  .widget_contentTitle {
    margin-left: 10px;
  }
  
  .widget_contentTitle > h5 {
    color: rgb(70, 68, 68);
  }
  
  .widget_contentTitle > p {
    color: gray;
    font-size: small;
  }
