.qHeader {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1px;
    background-color: rgb(233, 148, 148);
    width: 100%;
  
    position: sticky;
    z-index: 1000;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.5);
  }
  
  .qHeader_logo > img {
    height: 80px;
    margin-right: 20px;
  }
  
  .qHeader_icons {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-left:160px ;
    margin-top: -70px;
  }
  
  .qHeader_icons > .active > .MuiSvgIcon-root {
    fill: #8f1f1b;
  }
  
  .qHeader_icon > .MuiSvgIcon-root {
    font-size: xx-large;
    background-color: white;
    padding: 20px 60px;
    color: gray;
  
  }
  
  .qHeader_icon > .MuiSvgIcon-root:hover {
    background-color: grey;
    border-radius: 10px;
    color:#00ff37;
  }
  
  .qHeader_input {
    display: flex;
    align-items: center;
    border: 1px solid lightblue;
    padding: 5px;
    border-radius: 5px;
    margin-left: 5px;
    
  }
  
  .qHeader_input > input {
    background-color: transparent;
    outline: none;
    border: none;
    color: rgb(49, 49, 49);
  }
  
  .qHeader_input > .MuiSvgIcon-root {
    color: gray;
  }
  
  .qHeader_avatar {
    cursor: pointer;
  }
  .qHeader_Rem {
    display: flex;
    align-items: center;
    margin-left: 25px;
  }
  
  .qHeader_Rem > .MuiSvgIcon-root {
    font-size: xx-large;
    color: rgb(255, 7, 7);
    margin-left: 25px;
    cursor: pointer;
  }
  .qHeader_Rem > .MuiSvgIcon-root:hover {
    color: #fd170f;
  }
  
  .qHeader_Rem > .MuiButton-root {
    color: white;
    background: rgb(11, 106, 184);
    text-transform: inherit;
    border-radius: 20px;
    margin-left: 25px;
  }
  
  .qHeader_Rem > .MuiButton-root:hover {
    color: white;
    background-color: rgb(81, 160, 250);
  }
  .modal__title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    border-bottom: 1px solid rgba(187, 187, 187, 0.5);
  }
  
  .modal__title > h5 {
    color: gray;
    font-size: 20px;
    cursor: pointer;
    font-weight: 500;
    margin-right: 30px;
  }
  
  .modal__title > h5:hover {
    color: #b92b27;
  }
  .modal__info {
  display: flex;
  align-items: center;
  margin-top: 30px;
  }
  
  .modal__info > p {
  margin-left: 10px;
  font-size: small;
  color: gray;
  }
  
  .modal__info > .modal__scope {
  display: flex;
  align-items: center;
  color: rgb(98, 98, 98);
  padding: 5px;
  margin-left: 10px;
  background-color: rgb(230, 230, 230);
  border-radius: 33px;
  cursor: pointer;
  }
  .modal__Field {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  flex: 1;
  }
  
  .modal__Field > .modal__fieldLink {
  color: gray;
  display: flex;
  margin-top: 10px;
  align-items: center;
  }
  
  .modal__Field > .modal__fieldLink > input {
  flex: 1;
  border: none;
  outline: none;
  margin-left: 5px;
  } 
  .modal__buttons {
  display: flex;
  align-items: center;
  margin-top: 260px;
  margin-right: -20px;
  border-top: 1px solid lightgray;
  }
  
  .modal__buttons > .cancle {
  margin-left: auto;
  border: none;
  margin-top: 5px;
  outline: none;
  color: gray;
  font-weight: 500;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
  }
  .modal__buttons > .add {
  border: none;
  outline: none;
  margin-top: 5px;
  background-color: rgb(0, 64, 255);
  color: white;
  font-weight: 700;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
  }